@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);


body{
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

.search-input{
  background: #ff00cc; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(45deg, #ff00cc, #333399); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 15px;
  border: 4px solid rgba(255, 255, 255,0.5);
  color: #fff !important;
}


.search-input::-webkit-input-placeholder {
  color: #e2e2e2;
}


.search-input:-ms-input-placeholder {
  color: #e2e2e2;
}


.search-input::placeholder {
  color: #e2e2e2;
}

.search-input:focus{
  box-shadow: inset 0 0 0 #ddd;
}

.card{
  background: #ff00cc; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(30deg, #ff00cc, #333399); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 15px;
  position: relative;
  transition: .5s ease-in-out;
  cursor: pointer;
}

.card img{
  width:100px;
  height: 100px;
  position: absolute;
  top: -50px;
  right: 28%;
  background: #fff;
  padding: 10px;
  border-radius: 15px;
  
}

.card:hover{
  transform: translateY(-20px);
  box-shadow: 0 10px 20px rgba(255, 0, 204,.5);
}

@media only screen and (max-width: 600px) {
  .card img{
    right: 35%;
  }
}
